export default [{
  path: '/',
  redirect: '/customerReport'
}, {
  path: '/customerReport',
  component: () => import('@/views/customerTestDrive/report.vue'),
  meta: {
    title: '试驾图鉴'
  }
}, {
  path: '/customerValuation',
  component: () => import('@/views/customerTestDrive/valuation.vue'),
  meta: {
    title: '客户满意度调研'
  }
}, {
  path: '/result',
  component: () => import('@/views/customerTestDrive/result.vue'),
  meta: {
    title: '提交成功'
  }
}]