import Vue from 'vue'
import VueRouter from 'vue-router'
import routeList from './routeList'

Vue.use(VueRouter)

const router =  new VueRouter({
  routes: routeList
})

//路由后置守卫
router.afterEach((to) => {
  // 设置每个页面的标题
  to.meta.title && document && (document.title = to.meta.title)
})

export default router