/** 按需引入vant用到的组件 */

import Vue from 'vue'

import Button from 'vant/lib/button'
import 'vant/lib/button/style'

// 评分组件
import Rate from 'vant/lib/rate'
import 'vant/lib/rate/style'

// 输入框组件
import Field from 'vant/lib/field'
import 'vant/lib/field/style'

// toast
import vantToast from 'vant/lib/toast'
import 'vant/lib/toast/style'

// 弹窗
import vantDialog from 'vant/lib/dialog'
import 'vant/lib/dialog/style'

// 通知
import vantNotify  from 'vant/lib/notify'
import 'vant/lib/notify/style'

// header组件
import navBar from 'vant/lib/nav-bar'
import 'vant/lib/nav-bar/style'

Vue.component('van-button', Button)
Vue.component('van-rate', Rate)
Vue.component('van-field', Field)
Vue.component('van-nav-bar', navBar)

export const Toast = vantToast
export const Dialog = vantDialog
export const Notify = vantNotify
